
import { Ability, AbilityBuilder } from '@casl/ability';

import { store } from '../../redux/storeConfig/store'

const ability = new Ability([])
const { can, rules } = new AbilityBuilder();

function generateUserAbilities(USER_GROUPS) {
  let canArr = [];

  if (USER_GROUPS.length > 0) {
    USER_GROUPS.forEach(user_group => {
      if (user_group.GROUP_ROLES.length > 0) {
        user_group.GROUP_ROLES.forEach(group_role => {
          if (group_role.ACTIVE){
            if (group_role.READ_PERMISSION) { canArr.push(can("read", group_role.ROLE.ROLE_NAME, group_role.READ_PERMISSION)); }
            if (group_role.CREATE_PERMISSION) { canArr.push(can("create", group_role.ROLE.ROLE_NAME, group_role.CREATE_PERMISSION)); }
            if (group_role.UPDATE_PERMISSION) { canArr.push(can("update", group_role.ROLE.ROLE_NAME, group_role.UPDATE_PERMISSION)); }
            if (group_role.DELETE_PERMISSION) { canArr.push(can("delete", group_role.ROLE.ROLE_NAME, group_role.DELETE_PERMISSION)); }
            if (group_role.PRINT_PERMISSION) { canArr.push(can("print", group_role.ROLE.ROLE_NAME, group_role.PRINT_PERMISSION)); }
            if (group_role.APPROVE_PERMISSION) { canArr.push(can("approve", group_role.ROLE.ROLE_NAME, group_role.APPROVE_PERMISSION)); }
          }
        });
      }
    });
  }

  return canArr;
}

function defineRulesFor(user) {
  //console.log(user);
  if (user) {
    generateUserAbilities(user.USER_GROUPS)
  }

  return rules
}

let currentAuthUser;
(() => {
  const prevAuth = currentAuthUser;
  currentAuthUser = store.getState().auth.user;

  if (prevAuth !== currentAuthUser) {
    ability.update(defineRulesFor(currentAuthUser));
  }
})();

export default ability;