import React from "react"
import {
    Button
} from "reactstrap"
import DataTable from "react-data-table-component"
import TableCustomHeader from "../../../MainDataTable/TableCustomHeader";
import { Edit, Trash2 } from "react-feather"
import Can from '../../../../../utility/permissions/can';
class DepartmentTable extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Can I="update" a={this.props.role} passThrough>
                            { allowed => 
                                <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleEdit(row)}
                                >
                                    <Edit size={14} />
                                </Button.Ripple>
                            }
                        </Can>

                        <Can I="delete" a={this.props.role} passThrough>
                            { allowed => 
                                <Button.Ripple className="mr-1 mb-1  bg-gradient-danger" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleDelete(row)}
                                ><Trash2 size={14} />
                                </Button.Ripple>
                            }
                        </Can>
                    </>
                )
            },
            
            {
                name: "Department Code",
                selector: "DEPARTMENT_CODE",
                sortable: true,
                cell: row => <p className="mb-0">{row.DEPARTMENT_CODE}</p>
            },
            {
                name: "Department Name",
                selector: "DEPARTMENT_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.DEPARTMENT_NAME}</p>
            },
            {
                name: "Status",
                selector: "SYS_STATUS_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.SYSTEMSTATUS.SYS_STATUS_NAME}</p>
            }
        ]
    }

    render() {

        return (
            <>
            <DataTable
                className="dataTable-custom"
                data={this.props.value.length ? this.props.filteredData : this.props.data}
                columns={this.tableProperties.columns}
                noHeader
                pagination
                subHeader
                striped
                responsive
                subHeaderComponent={
                    <TableCustomHeader role={this.props.role} value={this.props.value} handleShow={this.props.handleShow} handleFilter={this.props.handleFilter} />

                }
            />
            </>

        )
    }
}


export default DepartmentTable