
import React from "react"
import {
  Input,
  Button,
} from "reactstrap"
import { Search } from "react-feather"
import Can from '../../../utility/permissions/can';

const TableCustomHeader = props => {
  return (
    <div className="d-flex flex-wrap justify-content-between w-100">
      <div className="add-new">
      <Can I="create" a={props.role} passThrough>
          { allowed => <Button.Ripple disabled={!allowed} className="mr-1 mb-1 bg-gradient-primary round" color="none" onClick={props.handleShow}>Add New</Button.Ripple> }
        </Can>
      </div>
      <div className="position-relative has-icon-left mb-1">
        <Input value={props.value} onChange={e => props.handleFilter(e)} />
        <div className="form-control-position">
          <Search size="15" />
        </div>
      </div>
    </div>
  )
}

export default TableCustomHeader
